.list {
    margin: 0;
    margin-left: 20px;
    padding: 0;
    margin-bottom: 20px;
    list-style-position: outside;
    font-size: 16px;
    list-style: var(--listStyle);
    > li {
        line-height: 1.6;
    }
    &.compact {
        margin-left: 0px;
        > li {
            line-height: 1.4;
        }
    }
}
