@import "../style/global.scss";

.inputField {
    font-size: 16px;
    @media only screen and (min-width: $screen-sm) {
        font-size: 19px;
    }
    > div {
        display: block;
    }
    .requiredSymbol {
        color: $color-error;
        font-size: 0.8em;
        vertical-align: top;
    }
    input,
    &.file span.input {
        @include appearance(none);
        @include transition(border-color 0.15s linear, background 0.15s linear);
        @include border-radius(0);
        @include box-shadow(none);
        height: 47px;
        width: 100%;
        display: block;
        padding: 10px;
        margin-bottom: 0;
        font-weight: 300;
        background-color: #fff;
        box-shadow: none;
        color: rgba(0, 0, 0, 0.75);
        font-family: inherit;
        font-size: 16px;
        box-sizing: border-box;
        border: 1px solid #afaba8;
        @media only screen and (min-width: $screen-sm) {
            font-size: 19px;
            height: 56px;
        }

        &:hover {
            border-color: $color-dark-blue;
        }

        &:focus {
            border-color: $color-dark-blue;
            outline-color: #54acb8;
        }

        &[disabled],
        &[readonly] {
            background-color: #ddd;
            cursor: default;
        }

        &.hasErrors {
            border-color: $color-error;
            &:focus {
                border-color: $color-error;
                outline-color: #b8565d;
            }
            &[disabled],
            &[readonly] {
                background-color: #ddd;
                cursor: default;
                border-color: #afaba8;
            }
        }
    }
    &.file {
        label {
            .fileInputContainer {
                display: flex;
                align-items: center;
            }
        }
        input {
            display: none;
        }
        span.input {
            flex: 1;
        }
        button {
            margin: 0 8px;
        }
    }
}
